<template>
    <section class="hero is-fullheight">
        <div class="hero-body">
            <div class="container">
                <div class="columns is-centered">
                    <div class="main">
                        <h1 class="title">Animateur</h1>
                        <div class="btn">
                                <button @click="startLive" id="live_btn">start live</button>
                                <button @click="stopLive" id="stop_btn">stop live</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
export default {
    data () {
        return{
            bool : false,
            liste_blob : [],
        }
    },
  methods : {
      startLive(){
        this.bool = true;
        let constraints = { audio: true };
        navigator.mediaDevices.getUserMedia(constraints).then(function(mediaStream) {
            var mediaRecorder = new MediaRecorder(mediaStream);
            mediaRecorder.onstart = function() {
                this.chunks = [];
            };
            mediaRecorder.ondataavailable = function(e) {
                this.chunks.push(e.data);
                var blob = new Blob(this.chunks, { 'type': 'audio/ogg; codecs=opus' });
                this.liste_blob.push(blob);
                if (bool) {
                    this.$socket.emit('radio', blob);
                }
            };
            console.log("record started");
            mediaRecorder.start();
            // console.log(this.chunks);

            setInterval(function() {
                if (this.bool) {
                    mediaRecorder.stop()
                    mediaRecorder.start()
                }
            }, 1000);
        });
      },
      stopLive(){
        this.bool = false;
        console.log("live stoped");
      }
   }
}
</script>

<style lang="scss">
.main {
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    .title{
        flex-basis : 100%;
        color : white;
    }
    .btn {
    flex-basis: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    #live_btn {
    background: rgb(223, 22, 22);
    border: none;
    border-radius: 3px;
    outline: none;
    cursor: pointer;
    width: 200px;
    height: 200px;
    margin-right: 2em;
}

#stop_btn {
    background: rgb(71, 63, 63);
    border: none;
    border-radius: 3px;
    outline: none;
    width: 200px;
    height: 200px;
    cursor: pointer;
    margin-right: 2em;
}
    }
}


</style>